<template>
	<div>
		<div class="wr" style="width: 400px;height: 400px">
			<div class="slider">
				<div class="btn-left" @click="left"></div>
				<div class="btn-right" @click="right"></div>
				<div class="items">
					<div class="item" v-for="a of arr" :key="a" :style="{backgroundImage:`url(images/${a})`}"  @dragstart="right"></div>
				</div>
			</div>

		</div>

	</div>
</template>

<script>
    export default {
        name: "Slider2",

        data() {
            return {
                arr: [
                    '1.jpg',
                    '2.jpg',
                    '3.jpg',
                    '4.jpg',
                    '5.jpg',
                    '6.jpg',
                    '7.jpg',
                    '8.jpg',
                    '9.jpg',
                    '10.jpg',
                ],

                next: 0,

            }
        },

        methods: {

            left() {

                let btnRight = document.querySelector('.btn-right');
                btnRight.style.transition = 'all .5s';
                btnRight.style.opacity = '1';

                let item = document.querySelector('.item');
                let w = item.offsetWidth;
                // item.style.height = w;
                let items = document.querySelector('.items');
                if (this.next > 0) {
                    this.next -= w;

                }
                items.scrollLeft = this.next;
                if (this.next < w) {
                    let btnLeft = document.querySelector('.btn-left');
                    btnLeft.style.transition = 'all .5s';
                    btnLeft.style.opacity = '0';
                }


            },


            right() {
                let btnLeft = document.querySelector('.btn-left');
                btnLeft.style.transition = 'all .5s';
                btnLeft.style.opacity = '1';

                let items = document.querySelector('.items');
                let children = items.childElementCount;
                let item = document.querySelector('.item');
                let w = item.offsetWidth;
                let widthItems = (w * children) - items.clientWidth;
                if (this.next < widthItems) {
                    this.next += w;
                }
                items.scrollLeft = this.next;
                if (this.next >= (widthItems)) {
                    let btnRight = document.querySelector('.btn-right');
                    btnRight.style.transition = 'all .5s';
                    btnRight.style.opacity = '0';

                }
            },
        },

    }
</script>

<style scoped lang="scss">


	.slider {
		position: relative;
		width: 100%;
		height: 100%;

		.btn-left {
			position: absolute;
			width: 35px;
			height: 35px;
			border-radius: 50%;
			background: transparentize(white, 0.4);
			box-shadow: 1px 1px 10px 1px black;
			border: 1px solid #999;
			top: 40%;
			left: 10px;
			cursor: pointer;
			z-index: 1000;
			opacity: 0;


			&:after {
				content: '';
				position: absolute;
				width: 14px;
				height: 14px;
				border: 4px solid #444444;
				border-radius: 4px;
				top: 0;
				left: 3px;
				right: 0;
				bottom: 0;
				margin: auto;
				border-bottom: none;
				border-right: none;
				transform: rotate(-45deg);
			}

		}


		.btn-right {
			position: absolute;
			width: 35px;
			height: 35px;
			border-radius: 50%;
			background: transparentize(white, 0.4);
			box-shadow: 1px 1px 10px 1px black;
			border: 1px solid #999;
			top: 40%;
			right: 10px;
			cursor: pointer;
			z-index: 1000;
			perspective: 800px;

			&:after {
				content: '';
				position: absolute;
				width: 14px;
				height: 14px;
				border: 4px solid #444444;
				border-radius: 4px;
				top: 0;
				left: 0;
				right: 3px;
				bottom: 0;
				margin: auto;
				border-bottom: none;
				border-right: none;
				transform: rotate(135deg);

			}

		}


		.items {
			/*border: 2px solid red;*/
			white-space: nowrap;
			overflow: hidden;
			scroll-behavior: smooth;
			width: 100%;
			height: 100%;


			.item {
				width: 100%;
				height: 100%;
				display: inline-block;
				background-size: cover;
				background-position: center;
			}
		}
	}

</style>