<template>
	<div>
		age Child 1 : {{ age }}
	</div>
</template>

<script>
    import {eventBus} from "../../main";

    export default {
        name: "child1",


        data() {
            return {
                age : null
            }
        },

        created() {
            eventBus.$on('changeAge', data => {
				this.age = data
            })
        }
    }
</script>

