import Vue from 'vue'
import App from './App.vue'
import router from './router'
import store from './store';
import vueResource from 'vue-resource';
import './registerServiceWorker'


export const eventBus = new Vue({
    methods: {
        changeAge(age) {
            this.$emit('changeAge', age);
        }
    }
});

Vue.use(vueResource);

Vue.config.productionTip = false

Vue.directive("custom", {
    bind(el, binding) {
        el.style.backgroundColor = binding.value.bg;
        el.style.color = binding.value.co;
        el.style.fontSize = binding.value.fs;
        // el.style.padding = binding.value.pa;

    }

});

Vue.filter('toLower', function (v) {
        return v.toLowerCase();
    }
)


export const BusEvent = new Vue()


new Vue({
    data() {
        return {
            assetUrl: 'http://localhost/dgkala/public/'
        }
    },
    router,
    store,
    render: h => h(App)
}).$mount('#app')
