<template>
	<div id="index">
		<div class="wrapper">
			<div class="items">
				<div class="item" v-for="(product,index) of products" :key="index">
					<div class="ratio"></div>
					<div class="image" :style="{backgroundImage:`url(http://localhost/parsa/public/images/products/${product.id}/${product.image})`}"></div>
					<div class="text">
						<p><span class="title"> نام کالا : </span>{{ product.name }}</p>
						<p><span class="title"> قیمت : </span>{{ product.price }} تومان</p>
					</div>
				</div>
			</div>
		</div>
		<!--		<button @click="load">page : {{ page }}</button>-->
		<infinite-loading @infinite="load" />
	</div>
</template>

<script>
    import InfiniteLoading from 'vue-infinite-loading';
    import axios from 'axios';

    export default {
        name: "index",

        components: {
            InfiniteLoading
        },

        data() {
            return {
                products: [],
                page: 1,
            }
        },

        methods: {
            load($s) {
                const vm = this;
                axios.get('http://localhost/parsa/public/api/products', {params: {page: vm.page}}).then(res => {
                    if (res.data.data.length > 0) {
                        vm.products.push(...res.data.data);
                        vm.page++;
                        $s.loaded();
                    }
                    else
                    {
                        $s.complete();
                    }
                }).catch(err => {
                    window.console.log(err)
                })
            }
        },

        // beforeRouteEnter(to, from, next) {
        //     axios.get('http://localhost/parsa/public/api/products').then(res => {
        //         next(vm => {
        //             vm.products = res.data.data;
        //             window.console.log(vm.products[0]);
        //         })
        //     })
        // },
    }
</script>

<style scoped lang="scss">
	#index {
		.items {
			display: flex;
			flex-direction: row-reverse;
			justify-content: flex-start;
			flex-wrap: wrap;
			margin: -10px;
			padding-top: 20px;

			.item {
				width: calc(25% - 20px);
				margin: 10px;
				flex: 0 0 auto;
				position: relative;
				background-color: #c9ff22;
				border-radius: 4px;
				box-shadow: 1px 1px 3px #999999;

				.ratio {
					padding-top: 100%;
					position: relative;
				}

				.image {
					width: 100%;
					height: 60%;
					background-color: orange;
					position: absolute;
					top: 0;
					left: 0;
					background-size: cover;
					background-position: center;
				}

				.text {
					direction: rtl;
					text-align: right;
					padding: 20px;

					p {
						.title {
							font-weight: bold;
							color: #000000;
						}
					}
				}
			}
		}
	}
</style>