<template>
    <div>
        <br>
        <input type="text" ref="inp">
        <br><br>
        <a  class="btn btn-success" @click="change">Change</a>
    </div>
</template>

<script>
    export default {
        name: "emit",

        props:{
            name : String,
        },

         data(){
            return{
                n : this.name,
            }
        },


        methods:{
            change(){
                this.n = this.$refs.inp.value;
                this.$emit('changeName',this.n);
            }
        },

    }
</script>

