<template>
    <div>
        <div class="container">
<!--            <router-link to="/create">Create</router-link>-->
            <div></div>
            <div class="slider">
                <div class="btn-left" @click="left" ref="left"></div>
                <div class="btn-right" @click="right" ref="right"></div>
                <div class="items">
                    <div class="item"
                         v-for="(a,i) of arr"
                         :key="i"
                         :style="{backgroundImage:`url(../images/${a.path})`}">

                        <div class="overlay"></div>

                    </div>
                </div>
            </div>
            <div style="height: 100px"></div>

        </div>
    </div>
</template>

<script>
    export default {
        name: "Slider",


        beforeRouteEnter(to,from,next){
            alert('Salam');
            window.console.log('welcome to Slider');
            next();
        },
        beforeRouteLeave(to,from,next){
            this.IsConfirm = confirm('Do you want to Exit?');
            if(this.IsConfirm)
            {
                next();
            }
            else
            {
                next(false);
            }
        },

        data() {
            return {
                IsConfirm : false,
                next: 0,
                arr: [
                    {path: '1.jpg'},
                    {path: '2.jpg'},
                    {path: '3.jpg'},
                    {path: '4.jpg'},
                    {path: '5.jpg'},
                    {path: '6.jpg'},
                    {path: '7.jpg'},
                    {path: '8.jpg'},
                    {path: '9.jpg'},
                    {path: '10.jpg'},
                ]
            }
        },


        methods: {

            left() {
                let btnRight = document.querySelector('.btn-right');
                btnRight.style.transition = 'all .5s';
                btnRight.style.opacity = '1';

                let items = document.querySelector('.items');
                if (this.next > 0) {
                    this.next -= 220;

                }
                items.scrollLeft = this.next;
                if(this.next < 220)
                {
                    let btnLeft = document.querySelector('.btn-left');
                    btnLeft.style.transition = 'all .5s';
                    btnLeft.style.opacity = '0';
                }


            },


            right() {
                let btnLeft = document.querySelector('.btn-left');
                btnLeft.style.transition = 'all .5s';
                btnLeft.style.opacity = '1';

                let items = document.querySelector('.items');
                let children = items.childElementCount;
                let item = document.querySelector('.item');
                let w = item.offsetWidth;
                let widthItems = ((w + 20) * children) - items.clientWidth;
                if (this.next < widthItems) {
                    this.next += 220;

                }
                items.scrollLeft = this.next;
                if(this.next > (widthItems) )
                {
                    let btnRight = document.querySelector('.btn-right');
                    btnRight.style.transition = 'all .5s';
                    btnRight.style.opacity = '0';

                }
            },


        }
    }
</script>

<style scoped lang="scss">

    .slider {

        position: relative;

        .btn-left {
            position: absolute;
            width: 45px;
            height: 45px;
            border-radius: 50%;
            background: #fff;
            box-shadow: 1px 1px 10px 1px black;
            border: 1px solid #999;
            top: 40%;
            left: -10px;
            cursor: pointer;
            z-index: 1000;
            opacity: 0;


            &:after {
                content: '';
                position: absolute;
                width: 14px;
                height: 14px;
                border: 4px solid #444444;
                border-radius: 4px;
                top: 0;
                left: 3px;
                right: 0;
                bottom: 0;
                margin: auto;
                border-bottom: none;
                border-right: none;
                transform: rotate(-45deg);
            }

    }


    .btn-right {
        position: absolute;
        width: 45px;
        height: 45px;
        border-radius: 50%;
        background: #ffffff;
        box-shadow: 1px 1px 10px 1px black;
        border: 1px solid #999;
        top: 40%;
        right: -10px;
        cursor: pointer;
        z-index: 1000;
        perspective: 800px;

        &:after {
            content: '';
            position: absolute;
            width: 14px;
            height: 14px;
            border: 4px solid #444444;
            border-radius: 4px;
            top: 0;
            left: 0;
            right: 3px;
            bottom: 0;
            margin: auto;
            border-bottom: none;
            border-right: none;
            transform: rotate(135deg);

        }

    }


    .items {
        white-space: nowrap;
        overflow: hidden;
        scroll-behavior: smooth;
        perspective: 800px;


        .item {
            width: 200px;
            height: 200px;
            border-radius: 10px;
            margin: 10px;
            display: inline-block;
            background-position: center;
            background-size: cover;
            background-origin: border-box;
            cursor: pointer;
            position: relative;
            transition: all .5s;


            .overlay {
                position: absolute;
                top: 0;
                left: 0;
                right: 0;
                bottom: 0;
                margin: auto;
                background-color: rgba(0, 0, 0, .2);
                opacity: 0;
                border-radius: 10px;
                transition: all .5s;
            }

            &:hover {
                .overlay {
                    opacity: 1;
                }

                //transform: rotateY(-12deg);
            }


        }

        .item:first-child {
            margin-left: 15px;
        }

    }

    }

</style>