import Vue from 'vue'
import Vuex from 'vuex'

// import counter from './counter'

Vue.use(Vuex)

export default new Vuex.Store({
    state: {
        name: 'reza nikafarin',
        counter: 2,

        value : 0,
    },

    getters: {
        doubleCounter: state => {
            return state.counter * 2;
        },
        getValue(state){
            return state.value;
        }
    },


    mutations: {
        increment(state,p) {
            return state.counter += p;
        },

        decrement(state,p) {
            return state.counter -= p;
        },

        setValue(state,newValue){
            return state.value = newValue;
        }
    },

    actions : {
        increment: ({commit}) =>{
            commit('increment');
        },

        decrement : ({commit}) => {
            commit('decrement');
        },

        asyncIncrement : ({commit}) =>{
            setTimeout(()=>{
                commit('increment')
            },2000)
        },
        asyncDecrement : ({commit}) =>{
            setTimeout(()=>{
                commit('decrement')
            },2000)
        }
    }

})
