<template>
    <div>
<!--        <show-image :img-url="$root.assetUrl + 'images/' + kala.image"></show-image>-->
<!--        <show-image :img-url=""></show-image>-->
        <img :src="$root.assetUrl + 'images/' + kala.image" alt="">
<!--        <button class="btn btn-success" @click="foundImagePath">Click</button>-->
        <div class="box">
                <div class="title">ویرایش اطلاعات</div>
                <div class="items">
                    <div class="item">
                        نام کالا : <input type="text" name="name" :value="kala.name" ref="name">
                    </div>
                    <div class="item">
                        کد کالا : <input type="text" name="code" :value="kala.code" ref="code">
                    </div>
                    <div class="item">
                        قیمت : <input type="text" name="price" :value="kala.price" ref="price">
                    </div>
                    <div class="item">
                        نام شرکت : <input type="text" name="company" :value="kala.company" ref="company">
                    </div>
                    <div class="item">
                        تصویر : <input type="file" name="image" @change="change">
                    </div>
                    <div class="item">
                        <button class="btn btn-success" @click="send">ذخیره
                        </button>
                    </div>
                </div>
        </div>
    </div>
</template>

<script>
    import axios from 'axios';
    // import ShowImage from "./ShowImage";
    export default {
        data(){
          return{
              kala : {},
              imagePath : '',
              data:{},
              file : null
          }
        },

        // components:{
        //   'show-image' : ShowImage
        // },
        
        methods:{

          change(event){
              this.file = event.target.files[0];
          },

          found(){
              let vm = this;
              axios.get('http://localhost/dgkala/public/api/kala/'+this.$route.params.id)
                  .then(function (res) {
                        vm.kala = res.data;
                  });
          },

            send(){
                let fd = new FormData();
                fd.append('image', this.file);
                fd.append('name', this.$refs.name.value);
                fd.append('code', this.$refs.code.value);
                fd.append('price', this.$refs.price.value);
                fd.append('company', this.$refs.company.value);
              axios({
                  method: "post",
                  url: 'http://localhost/dgkala/public/api/editkala/' + this.$route.params.id,
                  data: fd,
                  headers: {
                      'Content-Type': 'multipart/form-data'
                  }
              });


            },
        },
        
        
        mounted() {
            this.found();
        }
    }
</script>

