<template>
    <div>
            Name : <input type="text" ref="name">
            <br><br>
            Code : <input type="text" ref="code">
            <br><br>
            Price: <input type="text" ref="price">
            <br><br>
            Company: <input type="text"  ref="company">
            <br><br>
            Image: <input type="file" @change="change">
            <br><br>
        <button  @click="send">Click</button>
        <br><br>
        <router-link :to="{name:'getinfo'}"> باز گشت </router-link>
    </div>
</template>

<script>
    import axios from 'axios';

    export default {
        name: "Create",

        data(){
            return {
                file: null
            };
        },

        methods: {
            change(event){
                this.file = event.target.files[0];

                window.console.log(this.file)
            },
            send() {
                let fd = new FormData();

                fd.append('image', this.file);
                fd.append('name', this.$refs.name.value);
                fd.append('code', this.$refs.code.value);
                fd.append('price', this.$refs.price.value);
                fd.append('company', this.$refs.company.value);
                window.console.log(fd);
                axios({
                    method: 'post',
                    url: 'http://localhost/dgkala/public/api/kala',
                    data: fd,
                    headers: {
                        'Content-Type': 'multipart/form-data'
                    }
                });

                this.$refs.name.value = '';
                this.$refs.code.value = '';
                this.$refs.price.value = '';
                this.$refs.company.value = '';
                this.$refs.image.value = '';
            }
        },
    }
</script>

