<template>
    <div>
        <div class="parent">
            <input type="text" v-model="st" class="form-control-multi" @change="changeData">
            <div class="btn-active" @click="addArrayData">
                <div class="work">+</div>
            </div>
        </div>

        <br>
        <br>

        <div class="parent" v-for="(data,index) of arrayData" :key="index">
            <input type="text" :value="data" class="form-control-multi">
            <div class="btn-active" @click="deleteArrayData(data)">
                <div class="work">x</div>
            </div>
            <br><br>
        </div>
    </div>
</template>

<script>
    export default {
        name: "multi-input",

        props: {
            ArrayPhone: Array
        },

        data() {
            return {
                arrayData: this.ArrayPhone,
                st: ''
            }
        },

        methods: {
            addArrayData() {
                this.arrayData.push(this.st);
                this.st = "";
            },

            deleteArrayData(data) {

                let i = this.arrayData.indexOf(data);
                window.console.log('index = ' + i);
                this.arrayData.splice(i, 1);
            },

            changeData(){
                this.arrayData.push(this.st);
                this.st = "";
            }

        },

    }
</script>

<style scoped lang="scss">

    .parent {
        position: relative;

        .form-control-multi {
            direction: rtl;
            text-align: right;
            background-color: #F1F6F7;
            border: none;
            border-radius: 5px;

            &.ltr {
                direction: ltr;
                text-align: left;
            }
        }

        .btn-active {
            background-color: transparent;
            width: 20px;
            height: 20px;
            border-radius: 50%;
            position: absolute;
            left: 5px;
            top: 3px;
            bottom: 2px;
            right: auto;
            display: flex;
            justify-content: center;
            align-items: center;
            transition: all .5s;

            .work {
                font-weight: bold;
                font-size: 18px;
                position: relative;
                top: 1px;
                left: 0;
            }

            &:hover {
                background-color: #999999;
                cursor: pointer;
            }

        }
    }

</style>