<template>
    <div>
        <button @click="f2()">Click</button>
        <br>
        {{arr}}
        <hr>
        <input type="text" v-model="st" @input="f1">
        <br><br>
        <div v-for="(a,i) of arr" :key="i">{{a}}</div>
    </div>
</template>

<script>
    export default {
        name: "Select",

        data() {
            return {
                st: '',
                names: ['alireza', 'mohammad', 'ali', 'fatemeh', 'hasan', 'hosin', 'mahdi', 'farhad', 'hadi', 'mohsen'],
                arr: []

            }
        },

        methods: {

            f()
            {
                let vm = this;
                this.arr = this.names.filter(function (n) {
                    if( n.indexOf(vm.st) > -1)
                    {
                        return vm.names;
                    }

                })
            },

            f1(){
                for(let i=0;i<this.names.length;i++)
                {
                        this.arr = this.filter(this.names,this.st);
                }
            },


            filter(array,x){
                let a = [];
                for(let i=0;i<array.length;i++)
                {
                    if(array[i].indexOf(x) > -1)
                    {
                        a.push(array[i]);
                    }
                }
                return a;
            },


            f2() {
                this.arr = this.filter(this.names,'f');
            }

        },


        computed:{
            cnames : function () {
                let vm = this;
                return vm.names.filter((n)=>{
                    return n.indexOf(vm.st) > -1;
                })

            },

            cnames1 : function () {
                    let vm = this;
                    for (let i = 0; i < this.names.length; i++)
                    {
                        if (this.names[i].indexOf(this.st) > -1)
                        {
                            vm.arr[i] =  this.names[i];
                        }
                    }
                    return vm.arr;
                }
            }




    }
</script>

