<template>
    <div>
        <div class="wrapper">
            <div class="show">
                <div class="items">
                    <div class="item" v-for="(value,key,index)  of list" :key="index"
                        @click="addBasket(value)">
                        {{ value.name }} :  {{ value.price}}
                    </div>
                </div>
            </div>
<!--            <div class="clear-fix"></div>-->

            <div class="basket">
                <div class="cards">
                    <div class="card"  v-for="(value,key,index)  of baskets" :key="index">
                        {{ value.name }} :  {{ value.price}}
                    </div>
                </div>
            </div>
            <div class="clear-fix"></div>


        </div>
    </div>
</template>

<script>


    export default {
        name: "UseMulti",

        data() {
            return {


                list: [
                    {name: 'روغن', price: '14000'},
                    {name: 'ماکارونی', price: '10000'},
                    {name: 'برنج', price: '20000'},
                    {name: 'شامپو', price: '19000'},
                    {name: 'تاید', price: '18000'},
                    {name: 'خرما', price: '12000'},
                ],

                baskets :  [],
            }
        },


        methods:{
            addBasket(value){
                this.baskets.push(value);
                let i = this.list.indexOf(value);
                this.list.splice(i,1);
            }

        }
    }

</script>

<style scoped lang="scss">


    .wrapper {
        width: 1200px;
        margin: 0 auto;
        padding: 50px;
        border: 2px solid red;

        .show {
            float: left;

            .items {
                width: 400px;
                display: flex;
                flex-wrap: wrap;
                flex-direction: row-reverse;
                box-shadow: 1px 1px 10px 1px #999999;
                border-radius: 5px;
                background-color: greenyellow;
                margin: -15px;
                justify-content: flex-start;

                .item {
                    width: calc(33.33% - 30px);
                    height: 100px;
                    margin: 15px;
                    border-radius: 5px;
                    box-shadow: 1px 1px 5px 1px #999999;
                    justify-content: center;
                    align-items: center;
                    display: flex;

                }
            }
        }
        .basket {
            float: right;

            .cards {
                width: 400px;
                display: flex;
                flex-wrap: wrap;
                flex-direction: row-reverse;
                box-shadow: 1px 1px 10px 1px #999999;
                border-radius: 5px;
                justify-content: center;
                align-items: center;
                background-color: greenyellow;
                margin: -15px;
                justify-content: flex-start;

                .card {
                    width: calc(33.33% - 30px);
                    height: 100px;
                    margin: 15px;
                    border-radius: 5px;
                    box-shadow: 1px 1px 5px 1px #999999;
                    justify-content: center;
                    align-items: center;
                    display: flex;


                }
            }
        }
    }

    .clear-fix
    {
        clear: both;
    }
</style>