<template>
    <div>

         Name : {{fname}}
        <input type="text" ref="inp">
        <br><br>
        <button @click="f">Click</button>
    </div>
</template>

<script>
    import {BusEvent} from "../main";

    export default {
        // name: "Progress",

        data(){
            return {
                fname : this.name,
                index : 0
            }
        },

        props:{
            name : {
                type: String,
                default : 'Ali',
            }
        },

        methods: {
            f() {
                this.index++;
                this.fname = this.$refs.inp.value + " " + this.index;
                BusEvent.$emit('changeName', this.fname)
            },
        }


   }
</script>

