<template>
    <div>
        <div class="container">
            <table class="table table-hover table-bordered">
                <thead>
                    <th>شماره</th>
                    <th>نام کالا</th>
                    <th>کد کالا</th>
                    <th>قیمت</th>
                    <th>نام شرکت</th>
                    <th>ویرایش</th>
                    <th>حذف</th>
                </thead>
                <tbody>
                    <tr v-for="(kala,i) of kalas" :key="i">
                        <td>{{kala.id}}</td>
                        <td>{{kala.name}}</td>
                        <td>{{kala.code}}</td>
                        <td>{{kala.price}}</td>
                        <td>{{kala.company}}</td>
                        <td><router-link :to="{name:'editinfo',params:{id:kala.id}}"> ویراش</router-link></td>
<!--                        <td><router-link :to="{name:'deleteinfo',params:{id:kala.id}}">حذف</router-link></td>-->
                        <td><button @click="del(kala.id)">حذف</button></td>
                    </tr>
                </tbody>
                <tfoot>
                    <th colspan="7"><router-link :to="{name:'create'}">افزودن یه لیست</router-link></th>
                </tfoot>
            </table>
        </div>
    </div>
</template>

<script>
    import axios from 'axios';
    export default {
        name: "Getinfo",
        data() {
            return {
                kalas: [],
                record : {}
            }
        },
        methods: {
            getinfo() {
                var vm = this;
                axios.get('http://localhost/dgkala/public/api/kala').then(function (res) {
                    vm.kalas = res.data;
                    window.console.log(res.data);
                });
            },

            del(idRecord){

                axios.delete('http://localhost/dgkala/public/api/kala/'+ idRecord)
                .then(function () {
                    window.location.reload();
                });
            },


        },

        mounted() {
            this.getinfo();

        },

    }
</script>

<style lang="scss" scoped>

    table{
        direction: rtl;

        td,th
        {
            text-align: center;
        }
    }

</style>