<template>
    <div>
        <transition>
            <div class="box" v-if="show">
                <div class="btn-close" @click="changeStatus"></div>
                <input placeholder="نام">
                <div class="get-comment">
                    <textarea name="" id="" cols="30" rows="10" placeholder="متن دلخواه را وارد کنید:"></textarea>
                </div>
                <div class="btn btn-success btn-block">ارسال</div>
            </div>
        </transition>

    </div>
</template>

<script>
    export default {
        name: "NewComment",

        props: {
            show: Boolean
        },

        data() {
            return {
                status: this.show,
            }
        },

        methods: {
            changeStatus() {
                this.status = !this.show;
                this.$emit('changeStatus', this.status);
            }
        },
    }
</script>

<style scoped lang="scss">

    .box {
        background-color: white;
        border-radius: 5px;
        box-shadow: 5px 5px 5px 5px #999999;
        padding: 20px;
        margin: 30px 0 0 0;
        direction: rtl;
        text-align: right;
        position: relative;
        transition: all .5s;
        perspective: 800px;


        .btn-close {
            position: absolute;
            left: 10px;
            top: 24px;
            width: 22px;
            height: 22px;
            border-radius: 50%;
            transition: all .5s;

            &:before {
                content: 'x';
                position: absolute;
                top: -2px;
                right: 6px;
                left: 0;
                margin: auto;
                font-size: 18px;
            }

            &:hover {
                background-color: #999999;
                color: white;
                cursor: pointer;
            }

        }

        > input {
            width: 80%;
            margin: 20px 0;
        }


        textarea {
            width: 100%;
        }

        .get-comment {

        }
    }


    .v-enter
    {
        opacity: 0;
        transform: rotateX(90deg);
    }

    .v-enter-active
    {
    }

    v-enter-to
    {}

    .v-leave
    {}

    .v-leave-active
    {
        transform: rotateX(90deg);
    }

    .v-leave-to
    {
        opacity: 0;
    }

</style>