<template>
    <div>
        <h1>name  = {{ myname }}</h1>
        <br> <br>
        <emit1 :name="myname" @changeName="f"></emit1>
    </div>
</template>

<script>
    import emit from './emit';
    export default {
        name: "Testemit",

        components:{
          emit1 : emit
        },

        data(){
            return{
                myname : 'ali'
            }
        },

        methods:{
            f(event){
                this.myname = event;
            }
        },
    }
</script>

