<template>
	<div>
		<div class="items"  @mouseup="drag(index)">
			{{ data }}
			<br>
			{{ x }}
			<div class="item" v-for="(item,index) of data" :key="index" @click="selectIndex(index)">
				{{ item }}
			</div>
		</div>
	</div>
</template>

<script>
    export default {
        name: "drag",
        data() {
            return {
                data: [0, 1, 2, 3, 4, 5],
                x: 0,
                y: 0,
            }
        },


        methods: {
            selectIndex(index) {
                this.x = index;
            },
            drag(index) {
                let z = this.data[index];
                this.data[index] = this.data[this.x];
                this.data[this.x] = z;
            }
        },
    }
</script>

<style scoped lang="scss">
	.items {
		margin: 100px auto;
		width: 300px;
		background-color: #eee;
		padding: 20px;

		.item {
			border: 1px solid #000000;
			padding: 20px;
			border-radius: 5px;
			background-color: #fff;
			cursor: pointer;
		}
	}

</style>