<template>
	<div>
		age Child2: {{ age }}
		<br>
		<br>
		<div class="btn btn-success" @click="change">Change</div>
	</div>
</template>

<script>
    import {eventBus} from '../../main';

    export default {
        name: "Child2",

        data() {
            return {
                age: 28,
            }
        },


        methods: {
            change() {
                this.age = 30;
				// this.$emit('changeAge',this.age);
                eventBus.$emit('changeAge',this.age);
                // eventBus.changeAge(this.age);
            }
        }
    }
</script>

