<template>
	<div>
		<a class="btn btn-success" @click="show = !show">Click</a>

		<transition name="fade">
			<div class="box" v-if="show"></div>
		</transition>

	</div>
</template>

<script>
	export default {
		name: "Test-transition",

		beforeRouteEnter(to,from,next){
			alert('Hello Reza');
			window.console.log('From : ' + from.name);
			window.console.log('To : ' + to.name);
			next();
		},

		beforeRouteLeave(to,from,next){
			let isConfirm = window.confirm('Do You Want Leave This Page?');
			if(isConfirm)
			{
				next();
			}
			else
			{
				next(false);
			}
		},




		data(){
			return{
				show: false
			}
		}
	};
</script>

<style scoped lang="scss">
	.box
	{
		width: 300px;
		height: 200px;
		background-color: orange;
		border-radius: 10px;
		text-align: center;
		padding: 20px;
		box-sizing: border-box;
		margin: 50px auto;
		transition: all 1s;
		position: relative;
		left : 0;
		perspective: 800px;

		&:before
		{
			content: 'Box';
		}
	}


	.fade-enter
	{
		opacity: 0;
		left : -300px;
		transform: translateY(200px) rotateY(300deg);
	}

	.fade-enter-active
	{}

	.fade-enter-to
	{}

	.fade-leave
	{}

	.fade-leave-active
	{
	}


	.fade-leave-to
	{
		left : 300px;
		opacity: 0;
		transform: rotateY(300deg) translateY(200px);
	}

</style>