<template>
    <div>
        <div class="container">
            <div></div>
            <div class="slider">
                <div class="btn-left" @click="left" ref="left"></div>
                <div class="btn-right" @click="right" ref="right"></div>
                <div class="items row">
                    <div class="item"
                         v-for="(a,i) of arr"
                         :key="i"
                         :style="{backgroundImage:`url(../images/${a.path})`}">

                        <div class="overlay"></div>

                    </div>
                </div>
            </div>
            <div style="height: 100px"></div>

        </div>
    </div>
</template>

<script>
    export default {
        name: "Slider",

        props: {
            images: Array
        },

        data() {
            return {
                next: 0,
                arr: this.images,
            }
        },


        methods: {

            left() {

                let btnRight = document.querySelector('.btn-right');
                btnRight.style.transition = 'all .5s';
                btnRight.style.opacity = '1';

                let item = document.querySelector('.item');
                let w = item.offsetWidth;
                // item.style.height = w;
                let items = document.querySelector('.items');
                if (this.next > 0) {
                    this.next -= (w + 20);

                }
                items.scrollLeft = this.next;
                if (this.next < (w + 20)) {
                    let btnLeft = document.querySelector('.btn-left');
                    btnLeft.style.transition = 'all .5s';
                    btnLeft.style.opacity = '0';
                }


            },


            right() {
                let btnLeft = document.querySelector('.btn-left');
                btnLeft.style.transition = 'all .5s';
                btnLeft.style.opacity = '1';

                let items = document.querySelector('.items');
                let children = items.childElementCount;
                let item = document.querySelector('.item');
                let w = item.offsetWidth;
                let widthItems = ((w + 20) * children) - items.clientWidth;
                if (this.next < widthItems) {
                    this.next += (w + 20);
                }
                items.scrollLeft = this.next;
                if (this.next >= (widthItems)) {
                    let btnRight = document.querySelector('.btn-right');
                    btnRight.style.transition = 'all .5s';
                    btnRight.style.opacity = '0';

                }
            },
        },

        mounted() {
            // let item = document.querySelector('.item');
            // item.style.height = item.offsetWidth + 'px';
        },

    }
</script>

<style scoped lang="scss">

    @function size() {
        @return calc(33.33% - 20px);
    }

    .slider {

        position: relative;

        .btn-left {
            position: absolute;
            width: 45px;
            height: 45px;
            border-radius: 50%;
            background: #fff;
            box-shadow: 1px 1px 10px 1px black;
            border: 1px solid #999;
            top: 40%;
            left: -10px;
            cursor: pointer;
            z-index: 1000;
            opacity: 0;


            &:after {
                content: '';
                position: absolute;
                width: 14px;
                height: 14px;
                border: 4px solid #444444;
                border-radius: 4px;
                top: 0;
                left: 3px;
                right: 0;
                bottom: 0;
                margin: auto;
                border-bottom: none;
                border-right: none;
                transform: rotate(-45deg);
            }

        }


        .btn-right {
            position: absolute;
            width: 45px;
            height: 45px;
            border-radius: 50%;
            background: #ffffff;
            box-shadow: 1px 1px 10px 1px black;
            border: 1px solid #999;
            top: 40%;
            right: -10px;
            cursor: pointer;
            z-index: 1000;
            perspective: 800px;

            &:after {
                content: '';
                position: absolute;
                width: 14px;
                height: 14px;
                border: 4px solid #444444;
                border-radius: 4px;
                top: 0;
                left: 0;
                right: 3px;
                bottom: 0;
                margin: auto;
                border-bottom: none;
                border-right: none;
                transform: rotate(135deg);

            }

        }


        .items {
            white-space: nowrap;
            overflow: hidden;
            scroll-behavior: smooth;
            perspective: 800px;
            display: flex;
            flex-wrap: nowrap;


            .item {
                width: calc(100% - 20px);
                flex: 0 0 auto;

                /*height: 400px;*/

                border-radius: 10px;
                margin: 10px;
                display: inline-block;
                background-position: center;
                background-size: cover;
                background-origin: border-box;
                cursor: pointer;
                position: relative;
                transition: all .5s;


                .overlay {
                    position: absolute;
                    top: 0;
                    left: 0;
                    right: 0;
                    bottom: 0;
                    margin: auto;
                    background-color: rgba(0, 0, 0, .2);
                    opacity: 0;
                    border-radius: 10px;
                    transition: all .5s;
                }

                &:hover {
                    .overlay {
                        opacity: 1;
                    }
                }


            }

            .item:first-child {
                margin-left: 15px;
            }

        }

    }

