<template>
    <div>
        <h1>Add page</h1>
        <transition enter-active-class="animated rubberBand" leave-active-class="animated bounceOut">
            <div class="box" v-if="show">
                <br><br>
                Fname :<input type="text" ref="fname">
                <br><br>
                Lname :<input type="text" ref="lname">
                <br><br>
                Lname :<input type="text" ref="ave">
                <br><br>
                <button class="btn btn-success" @click="f1" style="margin-left: 10px">Save Info</button>
                <br><br>
                <router-link  :to="{name:'test'}">بازگشت</router-link>

            </div>
        </transition>

    </div>
</template>

<script>
    import axios from "axios";

    export default {
        name: "Add",

        data(){
            return{
                show : false
            }
        },

        mounted() {
            this.show  = true;
        },

        methods: {

            f1() {
                axios({
                    method: 'post',
                    url: 'http://localhost:8000/api/student',
                    data: {
                        fname: this.$refs.fname.value,
                        lname: this.$refs.lname.value,
                        ave: this.$refs.ave.value
                    }
                });
                this.$refs.fname.value  = '';
                this.$refs.lname.value = '';
                this.$refs.ave.value = '';
            }
        }

    }
</script>

<style scoped>
    .box {
        width: 300px;
        height: 300px;
        border-radius: 10px;
        margin: 10px auto;
        background-color: orange;
        position: relative;
        transition: all 1s;
        left: 0;
        top: 0;
    }

</style>