<template>
    <div>
        {{ api[0]}}
        <div class="main-image" :style="{backgroundImage:`url(../images/${api[index].img})`}">{{index}}</div>
        <div class="images">
            <div class="image" v-for="n in 5" :key="n"
                :style="{backgroundImage: `url(../images/${api[n-1].img})`}"
                 @click="index = n -1"
            >

            </div>
        </div>
    </div>
</template>

<script>
    export default {
        name: "SelectImage",
        data(){
            return{
                api :[
                    { img : '1.jpg'},
                    { img : '2.jpg'},
                    { img : '3.jpg'},
                    { img : '4.jpg'},
                    { img : '5.jpg'},
                ],

                index : 0,


            }
        },
    }
</script>

<style scoped lang="scss">


    .main-image
    {
        width: 300px;
        height: 300px;
        border-radius: 10px;
        margin : 50px auto;
        border: 1px solid red;
        background-size: cover;
        background-position: center;
        transition: background-image 1s;


    }

    .images
    {
        display: flex;
        justify-content: center;
        align-items: center;
        width: 700px;
        padding: 20px;
        height: 200px;
        border-radius: 10px;
        border: 1px solid red;
        margin: 50px auto;

        .image
        {
            flex: 1 1 auto;
            width: calc(20% - 20px);
            height: 100px;
            margin: 10px;
            border-radius: 5px;
            background-position: center;
            background-size: cover;
            transition: all 1s;


            &:hover
            {
                outline: 2px solid red;
                cursor: pointer;
            }
        }

