<template>
    <div>
        <h1>{{ number }}</h1>
        <button style="margin-right: 10px" class="btn btn-success" @click="increment">add</button>
        <span> </span>
        <button class="btn btn-success" @click="decrement">men</button>
    </div>
</template>

<script>

    export default {
        name: "age1",

        props:{
            age_prop : Number
        },


        data(){
            return{
                number : this.$store.state.num
            }
        },

        methods:{
            increment(){
                 this.$store.commit('inc');
                this.number =  this.$store.state.num;
            },
            decrement(){
                 this.$store.commit('dec');
                this.number =  this.$store.state.num;
            },
        }
    }
</script>

