<template>
    <div>
        <div class="box">
            <h2>age2 = {{ age }}</h2>

        </div>

        <button class="btn btn-success" @click="change">Change</button>
    </div>
</template>

<script>
    import {BusEvent} from "../main";

    export default {
        name: "age2",

        props:{
            age_prop : Number
        },


        data(){
            return{
                age : this.age_prop
            }
        },

        methods:{
            change : function () {
                this.age = "change by age2";
            }
        },


        created() {

            BusEvent.$on('changeAge',function(age){
                this.age = age;
            }.bind(this))
        }
    }
</script>

<style scoped >

    .box
    {
        width: 300px;
        height: 200px;
        background-color: orange;

    }

</style>