<template>
    <div>
        <div class="box">
            نام : <input type="text" :value="`${user.fname}`" ref="fname">
            <br><br>
            نام خانوادگی: <input type="text" :value="`${user.lname}`" ref="lname">
            <br><br>
            معدل: <input type="text" :value="`${user.ave}`" ref="ave">
            <br><br>
            <button class="btn btn-success" @click="update" >ذخیره</button>
        </div>

    </div>
</template>

<script>
    import axios from 'axios';

    export default {
        name: "Edit",

        data() {
            return {
                user: {}
            }
        },


        methods: {
            update() {
                axios({
                    method: 'put',
                    url: `http://localhost:8000/api/student/${this.user.id}`,
                    data: {
                        fname: this.$refs.fname.value,
                        lname: this.$refs.lname.value,
                        ave: this.$refs.ave.value
                    }
                });
            }
        },

        mounted() {
            var vm = this;
            axios.get('http://localhost:8000/api/student/' + this.$route.params.id)
                .then(function (res) {
                    vm.user = res.data;
                })
        }
    }
</script>

<style scoped lang="scss">

    html,body
    {
        outline: 0;
        border: 0;
        margin: 0;
        padding: 0;
    }



    .box
    {
        width: 400px;
        height: 300px;
        background-color: orange;
        border-radius: 10px;
        margin: 100px auto;
        direction: rtl;
        padding: 20px;
    }

</style>